<template>
  <swiper v-if="this.swiperSlidesCon.length > 0" :options="swiperOptionCon" class="swiper-con" ref="swiperCon">
      <swiper-slide v-for="(slide,index) in swiperSlidesCon" :key="index">
        <keep-alive>
          <component :is="slide.componentName" :current-idx="index"></component>
        </keep-alive>
      </swiper-slide>
    </swiper>
</template>

<script>
import Recommend from "@/views/Recommend";
import Article from "@/views/Article";

let vm = null;
export default {
  props: ["current"],
  data() {
    return {
      swiperSlidesCon: [
        {componentName: Recommend },
        {componentName: Article },
        {componentName: Article },
        {componentName: Article },
        {componentName: Article },
        {componentName: Article },
        {componentName: Article }
      ],
      swiperOptionCon: {
        initialSlide: this.current,
        autoplay: false,
        notNextTick: true,
        observer: true,
        observeParents: true,
        preloadImages: false,
        lazyLoading: true,
        resistanceRatio: 0,
        freeModeMinimumVelocity: 1,
        setWrapperSize: true,
        height: window.innerHeight,
        on: {
          slideChange() {
            var i = this.activeIndex;
            if (typeof i != "undefined") {
              vm.$emit("update:current", i);
            }
          },
          sliderMove(){
            vm.$root.eventHub.$emit('touchMoveFixed',true)
          },
          touchEnd(){
            vm.$root.eventHub.$emit('touchMoveFixed',false)
          }
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.swiperCon.swiper;
    }
  },
  watch: {
    current: {
      immediate: true,
      handler() {
        this.$nextTick(function() {
          this.$router.push({ path: "/", query: { id: this.current } });
          this.swiper.slideTo(this.current, 0, false)
        });
      }
    }
  },
  created() {
    vm = this;
  },
  mounted() {
  
  },
  methods:{
 
  },
  components: {
    Recommend,
    Article
  }
};
</script>

<style lang="scss" scoped>
.swiper-slide{
  min-height: 100vh;
}
</style>



