<template>
  <div>
      <home-banner :swiper-data="swiperData"></home-banner>

      <div v-for="item in this.activityData" :key="">
        <div class="title">
          <h3 v-text="item.type"></h3>
          <span @click="changeActivity(item.list,4)"><i class="fa fa-rotate-left"></i>换一换</span>
        </div>
        <ul class="ej-list clearfix">
          <li class="ej-item" v-for="(item2,index2) in item.list" :key="index2" v-show="showActivity(index2,4)">
            <router-link :to="{path:item2.url}">
              <div class="inner-img">
                <img :src="item2.img" alt="">
              </div>
              <div class="inner-con">
                <h3 class="ellipsis-2" v-text="item2.title"></h3>
                <p><span><i class="fa fa-clock-o"></i><em v-text="item2.time"></em></span><span class="ellipsis"><i class="fa fa-map-marker"></i><em v-text="item2.place"></em></span></p>
              </div>
            </router-link>
          </li>
        </ul>
      </div>

      <div v-for="(item,index) in this.inforData" :key="index">
        <div class="title">
          <h3 v-text="item.type"></h3>
        </div>
        <article-tpl :article-list="item.list"></article-tpl>
      </div>

  </div>
</template>

<script>
import ArticleTpl from '@/components/common/ArticleTpl'
import HomeBanner from '@/components/common/HomeBanner'

export default {
  data(){
    return{
      showIdx: 0,
      activityData: [],
      inforData: [],
      swiperData: [],
    }
  },
  mounted() {
    //模拟数据
    this.activityData = [
      {
        type: '活动推荐',
        list: [
          {url: '#1',img: 'http://temp.im/335x200/30407d/fff',title: '这是一段这是一段这是是是一段1',time:'2018-07-12',place: '上海黄浦'},
          {url: '#2',img: 'http://temp.im/335x200/27855d/fff',title: '这是一段测是一段测是一段测是一段测是一段测是一段测试2',time:'2018-07-12',place: '上海静安静安静安'},
          {url: '#3',img: 'http://temp.im/335x200/11407d/fff',title: '这是一段测这是一段试标题3',time:'2018-07-14',place: '上海浦东'},
          {url: '#4',img: 'http://temp.im/335x200/30337d/fff',title: '这是这是一段一段测试标题4',time:'2018-07-15',place: '上海闸北'},
          {url: '#5',img: 'http://temp.im/335x200/35677d/fff',title: '这是一这是一段段测试标题5',time:'2018-07-14',place: '上海普陀'},
          {url: '#6',img: 'http://temp.im/335x200/304345/fff',title: '这是一段测试标题6',time:'2018-07-13',place: '上海宝山'},
          {url: '#7',img: 'http://temp.im/335x200/308888/fff',title: '这是一段测试这是一段标题7',time:'2018-07-13',place: '上海金山'},
          {url: '#8',img: 'http://temp.im/335x200/4a4145/fff',title: '这是一段测试标题8',time:'2018-07-13',place: '上海奉贤'},
          {url: '#9',img: 'http://temp.im/335x200/30407d/fff',title: '这是一段这是一段这是是是一段9',time:'2018-07-12',place: '上海黄浦'},
          {url: '#10',img: 'http://temp.im/335x200/27855d/fff',title: '这是一段测试10',time:'2018-07-12',place: '上海静安'},
          {url: '#11',img: 'http://temp.im/335x200/11407d/fff',title: '这是一段测这是一段试标题11',time:'2018-07-14',place: '上海浦东'},
          {url: '#12',img: 'http://temp.im/335x200/30337d/fff',title: '这是这是一段段段段段段一段测试标题12',time:'2018-07-15',place: '上海闸北'},
        ]
      }
    ]
    
    this.inforData = [
      {
        type: '专题资讯',
        list: [
          {url: '#',img: 'http://temp.im/315x200/30407d/fff',title: '这是一段测试标题标题标题标题1',author:'by 小石',time:'2018-07-12',reading: '1000',good:'1000'},
          {url: '#',img: 'http://temp.im/315x200/27855d/fff',title: '这是一段测试标题标题标题标题2',author:'by 小石',time:'2018-07-12',reading: '1000',good:'9999'},
        ]
      },
      {
        type: '新闻资讯',
        list: [
          {url: '#1',img: 'http://temp.im/315x200/30407d/fff',title: '这是一段测试标题标题标题标题3',author:'by 小石',time:'2018-07-12',reading: '1000',good:'1000'},
          {url: '#2',img: 'http://temp.im/315x200/27855d/fff',title: '这是一段测试标题标题标题标题3',author:'by 小石',time:'2018-07-12',reading: '1000',good:'315'},
        ]
      },
    ]

    this.swiperData = [
      { url: "http://temp.im/750x250/30407d/fff" },
      { url: "http://temp.im/750x250/30407d/fff" },
      { url: "http://temp.im/750x250/30407d/fff" }
    ]
  },
  methods:{
    changeActivity(data,d){
      this.showIdx++;
      var len = data.length % d, max;
      if(len != 0){
        max = data.length / d + 1;
      }else{
        max = data.length / d;
      }
      if(this.showIdx > max - 1){
        this.showIdx = 0;
      }
    },
    showActivity(i,d){
      if(i >= this.showIdx * d && i < this.showIdx * d + d){
        return true;
      }else{
        return false;
      }
    },
  },
  components:{
    ArticleTpl,
    HomeBanner
  }
}
</script>

<style scoped="scss" lang="scss">
.title{
  display: flex;
  align-items: center;
  padding: px2rem(16) px2rem(32);
  h3{
    flex: 1;
    font-size: px2rem(36);
  }
  span{
    color: #6c6f71;
    font-size: px2rem(28);
    i{
      margin-right: px2rem(10);
    }
  }
}

.ej-list{
  padding: 0 px2rem(32);
  .ej-item{
    float: left;
    padding-bottom: px2rem(16);
    width: 50%;
    &:nth-child(2n+1){
      padding-right: px2rem(8);
    }
    &:nth-child(2n){
      padding-left: px2rem(8);
    }
    a{
      display: block;
      background-color: #fff;
      border: 1px solid #ddd;
    }
    .inner-img{
      img{
        display: block;
        width: 100%;
        height: px2rem(200);
      }
    }
    .inner-con{
      padding: px2rem(20) px2rem(8) px2rem(10);
      h3{
        font-size: px2rem(28);
        line-height: px2rem(32);
        height: px2rem(64);
      }
      p{
        text-align: right;
        white-space: nowrap;
        margin-top: px2rem(14);
        font-size: px2rem(24);
        color: #6c6f71;
        span{
          display: inline-block;
          vertical-align: middle;
          text-align: left;
          &:not(:last-child){
            margin-right: px2rem(16);
          }
          &:last-child{
            max-width: 40%;
          }
          i{
            margin-right: px2rem(8);
          }
        }
      }
    }
  }
}

</style>



