<template>
  <div class="container">
    <header class="header">
      <top-header></top-header>
      <top-nav :current.sync="current"></top-nav>
    </header>
    <main class="main">
      <swiper-con :current.sync="current"></swiper-con>
    </main>
  </div>
</template>

<script>
import TopNav from "@/components/common/TopNav";
import TopHeader from "@/components/common/TopHeader";
import SwiperCon from "@/components/common/SwiperCon";

export default {
  name: "Home",
  data() {
    return {
      current: 0
    };
  },
  watch: {
    current: {
      immediate: true,
      handler() {
       this.$root.eventHub.$emit('changeIdBus',this.current);
      }
    }
  },
  mounted() {
    this.current = this.$route.query.id || 0;
  },
  components: {
    TopNav,
    TopHeader,
    SwiperCon
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  z-index: 1120;
}

.main {
  padding-top: px2rem(188);
  padding-bottom: px2rem(111);
  height: 100%;
}
</style>



