<template>
   <nav class="nav">
     <keep-alive>
        <swiper v-if="this.swiperSlidesNav.length > 0" :options="swiperOptionNav" class="swiper-nav" ref="swiperNav">
            <swiper-slide v-for="(slide,index) in swiperSlidesNav" :key="index">
              <span v-text="slide.title" :class="{active:index == nowIdex}"></span>
            </swiper-slide>
        </swiper>
     </keep-alive>
    </nav>
</template>

<script>
let vm = null;
export default {
  props: ['current'],
 data() {
    return {
      nowIdex: this.current,
      swiperSlidesNav: [],
      swiperOptionNav:{
        initialSlide: this.current,
        freeMode : true,
        slidesPerView: 'auto', 
        observer:true,
        observeParents:true,
        on:{
          click: function(event){
            var i = this.clickedIndex
            if(typeof i != "undefined"){
              vm.$emit('update:current', i)
            }
          }
        }
      },
    }
 },
  created(){
    vm = this;
  },
  watch:{
    current:{
      immediate:true,
      handler(){
        this.nowIdex = this.current;
        this.$nextTick(function(){
          this.swiper.slideTo(Math.max(0, this.current - 1), 100, false);
        })
      }
    }
  },
  computed:{
    swiper() {
      return this.$refs.swiperNav.swiper;
    }
  },
  mounted() {
    //模拟tap数据
    var defaultData = [
      { title: "推荐"},
      { title: "新闻"},
      { title: "专访"},
      { title: "观点"},
      { title: "案例"},
      { title: "报告"},
      { title: "专题"}
    ]
    this.swiperSlidesNav = defaultData
  },
  methods:{
   
  }
}
</script>

<style lang="scss" scope>
.nav{
  height: px2rem(88);
}
.swiper-nav{
  background-color: #fff;
  .swiper-slide{
    display: inline-block;
    width: auto;
    &{
      span{
        display: block;
        padding: 0 px2rem(28);
        line-height: px2rem(88);
        font-size: px2rem(30);
        &.active{
          color:#f26d21;
        }
      }
    }
  }
}
</style>



