<template>
   <ul class="ej-list-2">
      <li class="ej-item-2" v-for="(item,index) in articleList" :key="index">
        <router-link :to="{path:item.url}">
          <div class="inner-img">
            <img :src="item.img" alt="">
          </div>
          <div class="inner-con">
            <h3 class="ellipsis-2" v-text="item.title"></h3>
            <p class="author" v-text="item.author"></p>
            <p class="data ellipsis"><span><em v-text="item.time"></em></span><span><i class="fa fa-eye"></i><em v-text="item.reading"></em></span><span><i class="fa fa-thumbs-o-up"></i><em v-text="item.good"></em></span></p>
          </div>
        </router-link>
      </li>
    </ul>
</template>

<script>
export default {
  props: ["articleList"],
  data() {
    return {

    };
  },
  watch: {
   
  },
  computed: {
    
  },
  methods: {
  }
};
</script>

<style lang="scss" scoped>
.ej-list-2 {
  .ej-item-2 {
    a {
      display: flex;
    }
    background-color: #fff;
    margin-bottom: px2rem(16);
    .inner-img {
      img {
        display: block;
        width: px2rem(315);
        height: px2rem(200);
      }
    }
    .inner-con {
      flex: 1;
      align-self: center;
      padding: 0 px2rem(16);
      min-width: 0;
      h3 {
        font-size: px2rem(32);
        line-height: px2rem(38);
        height: px2rem(76);
      }
      .author {
        margin-top: px2rem(30);
        color: #6c6f71;
      }
      .data {
        margin-top: px2rem(20);
        font-size: px2rem(24);
        color: #6c6f71;
        span {
          text-align: left;
          &:not(:last-child) {
            margin-right: px2rem(16);
          }
          i {
            margin-right: px2rem(8);
          }
        }
      }
    }
  }
}
</style>



