<template>
  <scroller class="scroller" :on-infinite="infinite" :on-refresh="refresh" :noDataText="noDataText" ref="my_scroller">
    <svg class="spinner" style="stroke: #f26d21" slot="refresh-spinner" viewBox="0 0 64 64"><g stroke-width="7" stroke-linecap="round"><line x1="10" x2="10" y1="27.3836" y2="36.4931"><animate attributeName="y1" dur="750ms" values="16;18;28;18;16;16" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="48;46;36;44;48;48" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values="1;.4;.5;.8;1;1" repeatCount="indefinite"></animate></line><line x1="24" x2="24" y1="18.6164" y2="45.3836"><animate attributeName="y1" dur="750ms" values="16;16;18;28;18;16" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="48;48;46;36;44;48" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values="1;1;.4;.5;.8;1" repeatCount="indefinite"></animate></line><line x1="38" x2="38" y1="16.1233" y2="47.8767"><animate attributeName="y1" dur="750ms" values="18;16;16;18;28;18" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="44;48;48;46;36;44" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values=".8;1;1;.4;.5;.8" repeatCount="indefinite"></animate></line><line x1="52" x2="52" y1="16" y2="48"><animate attributeName="y1" dur="750ms" values="28;18;16;16;18;28" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="36;44;48;48;46;36" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values=".5;.8;1;1;.4;.5" repeatCount="indefinite"></animate></line></g></svg>
    <article-tpl :article-list="this.currentData"></article-tpl>
    <svg class="spinner" style="stroke: #f26d21" slot="infinite-spinner" viewBox="0 0 64 64"><g stroke-width="7" stroke-linecap="round"><line x1="10" x2="10" y1="27.3836" y2="36.4931"><animate attributeName="y1" dur="750ms" values="16;18;28;18;16;16" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="48;46;36;44;48;48" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values="1;.4;.5;.8;1;1" repeatCount="indefinite"></animate></line><line x1="24" x2="24" y1="18.6164" y2="45.3836"><animate attributeName="y1" dur="750ms" values="16;16;18;28;18;16" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="48;48;46;36;44;48" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values="1;1;.4;.5;.8;1" repeatCount="indefinite"></animate></line><line x1="38" x2="38" y1="16.1233" y2="47.8767"><animate attributeName="y1" dur="750ms" values="18;16;16;18;28;18" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="44;48;48;46;36;44" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values=".8;1;1;.4;.5;.8" repeatCount="indefinite"></animate></line><line x1="52" x2="52" y1="16" y2="48"><animate attributeName="y1" dur="750ms" values="28;18;16;16;18;28" repeatCount="indefinite"></animate><animate attributeName="y2" dur="750ms" values="36;44;48;48;46;36" repeatCount="indefinite"></animate><animate attributeName="stroke-opacity" dur="750ms" values=".5;.8;1;1;.4;.5" repeatCount="indefinite"></animate></line></g></svg>
  </scroller>
</template>

<script>
import ArticleTpl from "@/components/common/ArticleTpl";

export default {
  props: ["currentIdx"],
  data() {
    return {
      page: 1,
      pagesize: 10,
      noDataText: "",
      currentData: [],
      position: false,
      tag: null,
    };
  },
  watch: {
    $route() {
      this.page = 1
      this.getData(this.page)
      // this.$refs.my_scroller.triggerPullToRefresh();
    },
    position: {
      immediate: true,
      handler() {
        this.$root.eventHub.$on("touchMoveFixed", isFixed => {
          this.position = isFixed
          if(this.position){
            this.$refs.my_scroller.touchMove = function(){}
          }else{
            this.$refs.my_scroller.touchMove = this.tag || function(){};
          }
        });
      }
    }
  },
  created() {
    
  },
  computed: {
   
  },
  mounted() {
    this.tag = this.$refs.my_scroller.touchMove;
    this.getData(this.page)
  },
  methods: {
    getData(page) {
      this.$http({
        url: "/api/list",
        type: "get",
        data: {
          id: this.currentIdx,
          page: page,
          pagesize: this.pagesize
        }
      })
        .then(resp => {
          if (resp.data.data != null && resp.data.data.length > 1) {
            if (page != 1) {
              this.currentData = this.currentData.concat(resp.data.data);
            } else {
              this.currentData = resp.data.data;
            }
            if (resp.data.data.length < this.pagesize) {
              this.noDataText = "没有更多数据";
              this.$refs.my_scroller.finishInfinite(true);
            } else {
              this.$refs.my_scroller.finishPullToRefresh();
              this.$refs.my_scroller.finishInfinite(false);
            }
          } else {
            if (page == 1) {
              this.noDataText = "暂无数据";
            } else {
              this.noDataText = "没有更多数据";
            }
            this.$refs.my_scroller.finishInfinite(true);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    infinite() {
      // this.noDataText = '加载中';
      // this.$refs.my_scroller.finishInfinite(true);
      this.getData(this.page++)
    },
    refresh() {
      this.page = 1
      this.getData(this.page)
    }
  },
  destroyed(){
    this.$root.eventHub.$off("touchMoveFixed");
  },
  components: {
    ArticleTpl
  }
};
</script>

<style lang="scss" scoped>
</style>



